import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';
dayjs.extend(duration);

const TimeFormat = {
  SIMPLE_TIME_FORMAT: 'hh:mm', //         (, 10:30  11:00)
  FORMAT_DATE_FROM: 'MMM DD', //   ,  ,  
  FORMAT_DATE_FOR_FORM_EDIT: 'YY/MM/DD HH:mm', // 18/03/19 12:25
  //         :
  FORMAT_MINUTES: 'mm[M]', //  :  (, 23M);
  FORMAT_ONE_DAY: 'HH[H] mm[M]', //  :   (, 02H 44M  12H 00M,    );
  FORMAT_DAYS: 'DD[D] HH[H] mm[M]', //  :    (, 51D 02H 30M  07D 00H 00M,   /   ).
};

const TimeInMilliseconds = {
  HOUR: 3600000,
  DAY: 86400000,
};

const humanizeDate = (date, formatDate) => date ? dayjs(date).format(formatDate) : '';

const getCorrectFormat = (dateFrom, dateTo) => {
  const eventDuration = dayjs(dateTo).diff(dateFrom);
  if (eventDuration < TimeInMilliseconds.HOUR) { //   
    return dayjs(eventDuration).format(TimeFormat.FORMAT_MINUTES);
  }
  if (eventDuration >= TimeInMilliseconds.HOUR & eventDuration < TimeInMilliseconds.DAY) { //   
    return dayjs(eventDuration).format(TimeFormat.FORMAT_ONE_DAY);
  }
  if (eventDuration >= TimeInMilliseconds.DAY) { //   
    return dayjs(eventDuration).format(TimeFormat.FORMAT_DAYS);
  }
};

const sortPointPrice = (pointA, pointB) => pointB.basePrice - pointA.basePrice;

const sortPointTime = (pointA, pointB) => {
  const eventDurationPointA = dayjs(pointA.dateTo).diff(pointA.dateFrom);
  const eventDurationPointB = dayjs(pointB.dateTo).diff(pointB.dateFrom);

  return eventDurationPointB - eventDurationPointA;
};

const sortPointDay = (pointA, pointB) => {
  const datePointA = new Date(pointA.dateFrom);
  const datePointB = new Date(pointB.dateFrom);

  return datePointA - datePointB;
};

export { humanizeDate, getCorrectFormat, TimeFormat, sortPointDay, sortPointPrice, sortPointTime};

